import React, { useState, useEffect } from 'react';
import {Helmet} from "react-helmet";
import { Link } from 'react-router-dom';
import { Nav, Section,Text,Bot,Icon,Space,Photo,LoadingD,PageIcon,CheckIcon } from '../Symbols.js'

export default (props)=>{
    const [filterMode,setFilterMode] = useState({orange:true,blue:true,green:true,gray:true})
    const [resData,setResData] = useState(null)
    const toggleFilter = (filter)=>{
        let newFilter = filterMode
        newFilter[filter] = !newFilter[filter]
        setFilterMode({...newFilter})
    }
    useEffect(() => {
        fetch(`https://raw.githubusercontent.com/designatuci/data/main/resources.json`)
            .then((res) => res.json())
            .then((data) => {
                setResData(data)
        })

    }, []);
    return <>
        <Nav/>
        <Helmet>
            <title>Featured Resources  Design at UCI</title>
        </Helmet>
        {/* <Section className="path bare">
            <div className="flex row top">
                <Link to="/resources/" style={{alignSelf:"flex-start"}}>
                    <Text>Resources</Text>
                </Link>
                <Text className="div">/</Text>
                <Link to="/resources/featured/" style={{alignSelf:"flex-start"}}>
                    <Text>Featured</Text>
                </Link>
            </div>
        </Section> */}
        <Section className="short">
            <div className="flex center spaceChildrenSmall">
                <Icon className="wait dx scale" src="featured-resources.svg" w="96" h="96"/>
                <Text className="wait dx scale" size="XL">Featured Resources</Text>
                <Text className="wait dx subtle color gray">A curated list of helpful tools, guides, and moreto help you practice your skills.</Text>
            </div>
        </Section>
        <Section className="bare center" style={{padding:"16px"}}>
            <div className="wait subtle flex row wrap">
            {[
                {title:"Tools",color:"orange",fill:"var(--orange"},
                {title:"Guides",color:"green",fill:"var(--green"},
                {title:"Inspiration",color:"blue",fill:"var(--blue"},
                {title:"Other",color:"gray",fill:"var(--gray)"},
            ].map((item,i)=>{
                return <a onClick={()=>{toggleFilter(item.color)}}>
                    <div className="flex row pointer" style={{padding:"0 24px"}}>
                        <CheckIcon check={filterMode[item.color]} r="18px" color={item.fill}/>
                        <Space block w="8"/>
                        <Text className={`color ${item.color}`}>{item.title}</Text>
                    </div>
                </a>
            })}
            </div>
        </Section>
        <Section className="short fill gray">
            { (resData==null) ?
                <div className="flex" style={{padding:"64px 0"}}>
                    <LoadingD width="128"/>
                </div>
            :
                <div className="split4" style={{gap:"16px"}}>
                    {resData.resources.map((card,i)=>{
                        if (filterMode[mapTypeColor(card.type)]) {return <a href={card.link} target="noreferrer" className="wait dx show card S flex left top fill white spaceChildrenSmall">
                            <div className="flex row spaceBetween" style={{margin:"-8px 0"}}>
                                <Text size="S" className={`color ${mapTypeColor(card.type)}`} >{card.type}</Text>
                                <PageIcon color="var(--silver)" style={{width:"32px",height:"32px"}}/>
                            </div>
                            <Space h="0"/>
                            <Photo className="shadow" style={{backgroundImage:`url(${(card.ogImg)?card.ogImg:`/static/photo/featured-resource.svg`})`,height:"128px",width:"100%"}}></Photo>
                            <Text size="L" className="color black">{card.title}</Text>
                            <Text className="color gray">{card.desc}</Text>

                        </a>}
                    })}
                </div>
            }
        </Section>
        <Bot />
    </>
}

function mapTypeColor(type) {
    switch (type) {
        case "Tool": return "orange"
        case "Inspiration": return "blue"
        case "Guide": return "green"
        default: return "gray"
    }
}